<template>
  <div class="pageContent">
    <!-- 筛选条件 -->
    <el-card class="filter-container" shadow="never">
      <div>
        <i class="el-icon-search"></i>
        <span>筛选搜索</span>
        <el-button style="float: right" @click="getData" type="primary" size="small">查询</el-button>
        <el-button style="float: right;margin-right: 15px" @click="reset" size="small"> 重置</el-button>
      </div>
      <div style="margin-top: 15px">
        <el-form :inline="true" :model="searchList" size="small" label-width="140px">
          <el-form-item label="门店名称：">
            <el-input style="width: 203px" v-model="searchList.roomName" clearable placeholder="门店名称"></el-input>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <!-- 数据列表 - head-->
    <el-card class="operate-container" style="margin-top:10px" shadow="never">
      <div class="flex-row-between" style="width:100%;">
        <div>
          <i class="el-icon-tickets"></i>
          <span>数据列表</span>
        </div>
        <div style="padding-right:20px;">
          <el-button type="text" icon="el-icon-circle-plus-outline" @click="$router.push({path:'/shop/shopManage/newShop'})">新增门店</el-button>
        </div>
      </div>
    </el-card>
    <!-- 数据列表 -->
    <el-card style="margin-top:20px;" class="filter-container" shadow="never">
      <el-table :data="list" border style="width: 100%">
        <el-table-column label="门店名称" prop="roomName"></el-table-column>
        <el-table-column label="联系地址">
          <template slot-scope="scope">
            {{scope.row.provinceName + scope.row.cityName + scope.row.areaName + scope.row.locationDesc}}
          </template>
        </el-table-column>
        <el-table-column label="联系电话" prop="telephone"></el-table-column>
        <el-table-column label="营业时间" prop="hourOperate"></el-table-column>
        <el-table-column label="门店状态">
          <template slot-scope="scope">
            <span v-if="!scope.row.invalid">启用</span>
            <span v-if="scope.row.invalid" style="color:#F56C6C;">停用</span>
          </template>
        </el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button type="text" size="small" @click="$router.push({path:'/shop/shopManage/editShop',query:{id:scope.row.id}})">编辑</el-button>
            <el-button @click="changeType(scope.row.id,0)" v-if="scope.row.invalid" type="text" size="small">启用</el-button>
            <el-button @click="changeType(scope.row.id,1)" style="color:#F56C6C;" v-if="!scope.row.invalid" type="text" size="small">停用</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!-- 分页 -->
    <div class="pageDown">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :page-sizes="[5, 10, 15, 20]"
        :page-size="searchList.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="listTotal">
      </el-pagination>
    </div>
    
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { 
  selectShopRoomList,
  invalidShopRoomById
} from '@/api/shop/shopManage'
export default {
  data(){
    return{
      searchList:{
        pageNum:1,
        pageSize:5
      },              // 搜索栏数据
      list:[],                    // 列表数据
      listTotal:0
    }
  },
  created(){
    this.searchList.shopId = this.shop.shopId
    this.getData()
  },
  methods:{
    // 数据列表
    getData(){
      selectShopRoomList(this.searchList).then(res=>{
        console.log('获取列表',res)
        this.list = res.data.list
        this.listTotal = res.data.total
      })
    },
    // 搜索栏重置
    reset(){
      this.searchList.roomName = ''
      this.getData()
    },
    changeType(id,t){
      this.$confirm(`此操作将${t==0 ? '启用' : '停用'}该门店, 是否继续?`, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        invalidShopRoomById(id,t).then(res=>{
          console.log('启用停用店铺',res)
          if(res.code == 200){
            this.getData()
            this.$message({
              message: '操作成功',
              type: 'success'
            });
          }else{
            this.$message.error(res.message);
          }
        })
      }).catch(() => {});
      
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.searchList.pageSize = val;
      this.getData()
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.searchList.pageNum = val;
      this.getData()
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>
<style lang="less" scoped>
  .pageDown{
    padding: 20px 0;
  }
</style>